.myBtn {
  height: 50px;
  width: 160px;
  color: rgb(0, 0, 0);
  font-size: 14px;
  background: rgb(255, 122, 204);
  font-size: 14px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  border-radius: 100px;
  transition: .2s ease-in-out background;
}

.myBtn:hover {
  background: rgb(129, 255, 238);
}

.active {
  height: 50px;
  width: 160px;
  color: rgb(0, 0, 0);
  font-size: 14px;
  font-size: 14px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  border-radius: 100px;
  transition: .2s ease-in-out background;
  background: rgb(129, 255, 238);
}